@import "@/styles/main.scss";








































































































.flickity-container{
    height: 110vh !important;
    margin-top: 60px;
    padding-top: 80px;
    padding-bottom: 100px;
    display: flex;
    flex-direction: column;
    width: 100%;
    background-image: url(~@/assets/back-test.svg);
    background-size: cover;

    .testimonial-head{
      width: 100%;
      padding: 0 50px;
      margin-bottom: 50px;

      .testimonial-head-title{
        font-size: 2.5rem;
        font-weight: bold;
        color: #ffffff;
        cursor: default;

        .love-us{
          border-bottom: 3px solid #F07A6A;
          font-style: normal;
        }
      }

      .testimonial-head-sub{
        font-size: 1.3rem;
        font-weight: normal;
        color: #ffffff !important;
        cursor: default;

      }
    }

    .flickity{
      // padding: 0 30px;
      // padding-bottom: 120px !important;

        /* no circle */
      .flickity-button {

        background: transparent;
        display: block !important;


      }

      /* big previous & next buttons */
      .flickity-prev-next-button {
        display: block !important;
        width: 40px;
        height: 40px;
      }

      /* icon color */
      .flickity-button-icon {

        fill: rgba($color: #ffffff, $alpha: 0.5);
      }

      .flickity-button-icon:hover {

        fill: #ffffff;
      }

      .testimonial-container.is-selected{
        opacity: 1;
      }

      .testimonial-container{

        width:20%;
        align-content: center;
        height: 340px;
        margin-right: 150px ;
        margin-top: 30px;
        opacity: 0.6;
        transition: opacity 0.4s;
        transition: transform 0.4s;
        background-image: url(~@/assets/comma.png);
        background-repeat: no-repeat;
        background-position: bottom right;
        background-size: 15%;
        z-index: -10;
        overflow: visible !important;

        .flickity-viewport{
          height: 300px !important;
          overflow: visible !important;
          z-index: 10;

        }

        .flickity-slider{
          height: 300px !important;
          z-index: 10;

        }

        .testimonial-name{
          font-size: 1.4rem;
          color: #ffffff;
          font-weight: 500;
          margin-bottom: 15px;
        }

        .testimonial-text{
          width: 110%;
          font-size: 1.1rem;
          line-height: 2.3rem;
          font-weight: 400;
          word-wrap: normal !important;
        }

        .testimonial-img{

            border-radius: 100px;
            margin-bottom: 10px;
        }

      }
    }
}

@media (max-width: 1100px){
  .flickity-container{
    margin-top: 0px;
  }
}

@media (max-width: 940px){
  .flickity-container{
    margin-top: 0px;
  }

  .testimonial-container{
    width: 35% !important;
  }
}

@media (max-width: 760px){
  .flickity-container{
    margin-top: 0px;


    .testimonial-container{
      width: 45% !important;
    }

    .testimonial-head{

        .testimonial-head-title{
          font-size: 2.1rem;
        }
    }
  }
}

@media (max-width: 665px){
  .flickity-container{
    margin-top: 0px;
  }

  .testimonial-container{
    width: 55% !important;
  }
}

@media (max-width: 545px){
  .flickity-container{
    margin-top: 0px;

    .testimonial-head{
      margin-bottom: 10px !important;

        .testimonial-head-title{
          font-size: 1.9rem;
        }
    }
  }

  .testimonial-container{
    width: 75% !important;
  }

  .testimonial-head{
    padding: 0 10px;
  }
}


@media (max-width: 516px){

  .flickity-container{
    height: 120vh !important;

    .testimonial-text{
      font-size: 1.1rem !important;
      line-height: 1.7rem !important;
    }
  }

}

@media (max-width: 411px){

  .flickity-container{
    height: 115vh !important;

    .testimonial-head{

        .testimonial-head-title{
          font-size: 1.7rem;
        }

        .testimonial-head-sub{
          font-size: 1.1rem;

        }
    }
  }

}

@media (max-width: 372px){

  .flickity-container{
    height: 145vh !important;
  }

}

@media (max-width: 500px){
  .flickity-container{
    margin-top: 0px;
    // padding-bottom: 100px !important;
  }

  .testimonial-container{
    width: 80% !important;
    height: 350px !important;
    opacity: 1 !important;

  }
}
@media (max-width: 380px){
  .flickity-container{
    padding-bottom: 250px !important;
  }

  .flickity-container .flickity .flickity-prev-next-button .previous{
      top: 70% !important;
  }
  .flickity-prev-next-button .next{
      top: 70% !important;

  }

  .testimonial-container{
    margin-top: 0 !important;
  }
}
@media (max-width: 360px){
  .flickity-container{
    padding-bottom: 230px !important;
  }
}
