@import "@/styles/main.scss";









































































































































































































































































.our-work-container{

    min-height: 102vh;
    margin-bottom: 100px;
    padding-top: 40px;
    padding-bottom: 80px;
}

.our-work-header{
    padding-top: 40px;
    margin-bottom: 40px;
    .our-work-heading{
        font-size: 2.5rem;
        font-weight: 600;
        color: #000000;
        cursor: default;
        margin-bottom: 0;

        span{
            color: #F07A6A;
        }
    }

    .our-work-heading-sub{
        font-size: 1.3rem;
        font-weight: 300;
        color: #808080;
        cursor: default;
    }
}

.flickity-button {
    display: none;
}

.flickity{
    height: 300px !important;
    overflow: visible !important;
    border-radius: 10px;
    margin-bottom: 150px;

}

.our-work-card.is-selected{
    transform: translateY(-20px);
    // height: 390px !important;
    z-index: 1000000;
    border-radius: 10px !important;
    overflow: hidden !important;


}

.our-work-card{

    width: 600px;
    height: 360px;
    box-shadow:
        0 2.8px 2.2px rgba(0, 0, 0, 0.034),
        0 6.7px 5.3px rgba(0, 0, 0, 0.048),
        0 12.5px 10px rgba(0, 0, 0, 0.06),
        0 22.3px 17.9px rgba(0, 0, 0, 0.04),
        0 41.8px 33.4px rgba(0, 0, 0, 0.01);

    cursor: pointer !important;
    background-size: 100%;
    background-repeat: no-repeat;
    border-radius: 10px !important;
    overflow: hidden !important;
    height: 390px;
    margin: 120px 30px 30px;
    transition: transform 1s ease;
    display: flex;
    justify-content: center;
    align-items: flex-end !important;
}

.bottom-card-section{
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    height: 80px;
    padding: 0 25px 20px;
    overflow: hidden !important;
    width: 100%;
    justify-content: space-between;
    align-items: flex-end;
    background: linear-gradient(to bottom, transparent 0%, transparent 8.1%,
    rgba(0,0,0,0.0005) 15.5%,
    rgba(0,0,0,0.0001) 22.5%,
    rgba(0,0,0,0.001) 29%,
    rgba(0,0,0,0.008) 35.3%,
    rgba(0,0,0,0.011) 41.2%,
    rgba(0,0,0,0.014) 47.1%,
    rgba(0,0,0,0.016) 52.9%,
    rgba(0,0,0,0.019) 58.8%,
    rgba(0,0,0,0.022) 64.7%,
    rgba(0,0,0,0.025) 71%,
    rgba(0,0,0,0.027) 77.5%,
    rgba(0,0,0,0.029) 84.5%,
    rgba(0,0,0,0.03) 91.9%,
    rgba(0,0,0,0.01) 100%);
    background-image: url( ~@/assets/shadow.svg ) , url( ~@/assets/shadow.svg );
    background-blend-mode: multiply;
    background-size: cover;

    .work-des{
        font-size: 1.1rem;
        color: rgba($color: #ffffff, $alpha: .85);
        font-weight: 400;
        padding: 0 0 5px;
        margin: 0;
    }

    a{
        color: #ffffff;
        text-decoration: none;

        &:hover{
            text-decoration: none;
            color: #ffffff;
        }
    }
    .preview{
        font-size: 1rem;
        font-weight: 400;
        padding: 6px 12px;
        border: 1px solid #ffffff;
        letter-spacing: 0.08rem;
        border-radius: 12px;
        background: transparent;
        outline: none;
        color: #ffffff;
        margin-bottom: 0px;
        transition: 0.4s;

        &:hover{
            background-color: #ffffff;
            color: #000000;
        }
    }
}

@media (max-width: '768px') {

    .our-work-heading{
        font-size: 30px !important;
        padding: 0 21px !important;
    }

    .flickity-button {
        display: none !important;
    }

    .our-work-card{
        width: 500px !important;
        height: 300px !important;
        margin: 100px 10px 0px;

    }

    .our-work-container{
        margin-bottom: 0 !important;
        padding-bottom: 0 !important;
    }
    .flickity{
        margin-bottom: 0 !important;
        height: 420px !important;
    }
}

@media (max-width: '550px') {
    .our-work-card{
        width: 450px !important;
        height: 270px !important;
        margin: 50px 10px 30px;
    }

    .flickity-button {
        display: none !important;
    }

    .flickity{
        margin-bottom: 0 !important;
        height: 420px !important;
    }

    .bottom-card-section{
        height: 50px;
    }

    .work-des{
        font-size: 1rem !important;
    }

    .preview{
        font-size: 0.95rem !important;
        padding: 5px 10px !important;
    }

}

@media (max-width: '480px') {
    .our-work-card{
        width: 400px !important;
        height: 240px !important;
    }

    .bottom-card-section{
        height: 45px;
    }

    .work-des{
        font-size: 1rem !important;
    }

    .preview{
        font-size: 0.9rem !important;
        padding: 4px 8px !important;
    }

}

@media (max-width: '430px') {
    .our-work-container{
        min-height: 95vh;
    }
    .our-work-card{
        width: 310.5px !important;
        height: 216px !important;
        margin: 60px 14px 30px;

    }

    .our-work-header{
        padding-top: 0 !important;
    }

    .flickity{
        margin-bottom: 0 !important;
        height: 400px !important;
    }

    .flickity-button {
        width: 30px !important;
        height: 30px !important;
        top: 40% !important;
    }

    .bottom-card-section{
        height: 50px !important;
        padding-bottom: 10px !important;
    }

    .work-des{
        font-size: 0.9rem !important;
        padding-bottom: 3px !important;
    }

    .preview{
        font-size: 0.7rem !important;
        padding: 5px 9px !important;
        border-radius: 8px !important;
    }

}

@media (max-width: '380px') {
    .our-work-container{
        min-height: 95vh;
        // padding-top: 200px !important;
    }
    .our-work-card{
        width: 262.5px !important;
        height: 166px !important;
        margin-bottom: 30px !important;
    }

    .flickity-prev-next-button.next {
        right: 5px;
    }

    .flickity-prev-next-button.previous {
        left: 5px;
    }

    .flickity{
        margin-bottom: 0 !important;
        height: 340px !important;
    }

    .flickity-button {
        width: 30px !important;
        height: 30px !important;
        top: 45% !important;
    }

    .bottom-card-section{
        height: 45px !important;
        padding-bottom: 10px !important;
    }

    .work-des{
        font-size: 0.7rem !important;
    }

    .preview{
        font-size: 0.6rem !important;
        padding: 3px 7px !important;
    }

}

@media (max-width: '380px') {
    .our-work-card{
        margin: 70px 7px 30px;
    }

    .flickity{
        margin-top: 0 !important;
        height: 200px !important;
    }
}

